/* eslint-disable import/prefer-default-export */
import * as yup from 'yup';

export const validationsSchema = yup.object().shape({
  name: yup.string()
    .typeError('  ')
    .required(' ')
    .min(2, '  2 ')
    .max(20, '  20 '),
  phone: yup.string()
    .typeError('  ')
    .required(' '),
});